<template>
  <div class="page">
      <!-- 头部导航 -->
      <Header title="添加地址"/>
      <div class="form">
        <div class="userName">
          <div class="left">联系人</div>
          <input type="text" placeholder="你的名字"  v-model="params.name">
        </div>
        <div class="gender">
          <div class="left"></div>
          <i class="iconfont icon-check1" @click="changeSex(1)" :class="{active:params.sex===1}"></i>
          <span>先生</span>
          <i class="iconfont icon-check1" @click="changeSex(2)" :class="{active:params.sex===2}"></i>
          <span>女士</span>
        </div>
        <div class="tele">
          <div class="left">联系电话</div>
          <input type="text" placeholder="你的手机号" v-model="params.phone">
          <i class="iconfont icon-plus" @click="addPhoneBk=true"></i>
        </div>
        <div class="tele" v-show="addPhoneBk">
          <div class="left"></div>
          <input type="text" placeholder="备选电话" v-model="params.phone_bk">
        </div>
        <div class="address">
          <div class="left">送餐地址</div>
          <input type="text" placeholder="小区/写字楼/学校等" v-model="params.address">
        </div>
        <div class="detail">
          <div class="left"></div>
          <input type="text" placeholder="详细地址（如门牌号等）" v-model="params.address_detail">
        </div>
        <div class="tag">
          <div class="left">标签</div>
          <input type="text" placeholder="无/家/学校/公司" v-model="params.tag">
        </div>
      </div>
      <div class="conform">
        <button @click="sendAddAddress">确定</button>
      </div>
  </div>
</template>

<script>
//引入添加地址接口
import request from '../../api/index'
//引入头部跳转组件
import Header from '../../components/Header'
export default {
  name: 'addAddress',
  components:{
    Header
  },
  data() {
    return {
      //发送请求所需的params参数
      params:{
        address: "红旗米线村(温都水城店)",
        address_detail: "3楼",
        geohash: "40.102281,116.365364",
        name: "mmm",
        phone: "188888888888",
        phone_bk: "",
        poi_type: 0,
        sex: 1,
        tag: "餐馆",
        tag_type: 2,
      },
      addPhoneBk:false
      // address: "红旗米线村(温都水城店)"
      // address_detail: "3楼"
      // geohash: "31.22299,121.36025"
      // name: "yyy"
      // phone: "18683257353"
      // phone_bk: ""
      // poi_type: 0
      // sex: 1
      // tag: "餐馆"
      // tag_type: 1
    }
  },
  methods:{
    //选择性别标签
    changeSex(sex){
      console.log(sex);
      //如果点击的不是已选择的性别才修改
      if(this.params.sex!==sex){
        this.params.sex = sex
      }
    },
    //发送添加地址请求
    async sendAddAddress(){
      try {
        const result = await request.addAddress(this.params,64931)
        console.log(result);
        if(result.status===1){
          this.$router.back()
        }
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang='less' scoped>
.page{
    background-color: #f5f5f5;
    width: 100%;
    height: 100vh;
    padding-top: 1.95rem;
    box-sizing: border-box;
    .form{
      width: 100%;
      &>div{
        position: relative;
        display: flex;
        width: 100%;
        height: 2.5rem;
        background-color: #fbfbfb;
        border-bottom: 2px solid #f1f1f1;
        &>div.left{
          width: 4.2rem;
          font-size: .8rem;
          padding-left: .7rem;
          line-height: 2.5rem;
        }
        &>input{
          border: none;
          outline: none;
          font-size: .65rem;
        }
        i.icon-plus{
          position: absolute;
          top: 0.5rem;
          right: 0.5rem;
          color: #3190e8;
          font-size: 1.2rem;
        }
        i.icon-check1{
          &.active{
            color: #4cd964;
          }
          line-height: 2.5rem;
          margin: 0 .4rem;
          font-size: 1.2rem;
          color: #ccc;
          font-weight: bold;
        }
        span{
          font-size: .7rem;
          line-height: 2.5rem;
        }
      }
    }
    .conform{
      padding: 1rem;
      display: flex;
      justify-content: center;
      button{
        width: 98%;
        height: 2rem;
        border: none;
        color: #fbfbfb;
        background-color: #4cd964;
        border-radius: .2rem;
      }
    }
}
</style>
