<template>
  <div class="page">
      <!-- 头部 -->
      <Header title="订单列表"/>
      <!-- 底部导航 -->
      <NavBar/>
      <div class="order" v-for="(item, index) in orderList" :key="index">
        <div class="left">
          <img :src="'https://elm.cangdu.org/img/'+item.restaurant_image_url">
        </div>
        <div class="right">
          <div class="status">
            <div class="detail">
              <p class="title">{{item.restaurant_name}}<i class="sui-icon icon-pc-chevron-right icon"></i></p>
              <p class="time">{{item.formatted_created_at}}</p>
            </div>
            <span>{{item.status_bar.title}}</span>
          </div>
          <div class="money">
            <span class="title">{{item.basket.group[0][0].name}}</span>
            <span class="price">¥{{item.total_amount}}</span>
          </div>
          <div class="more">
            <button>再来一单</button>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import request from '../../api/index'
import Header from '../../components/Header'
import NavBar from '../../components/NavBar'
export default {
  name: '',
  data() {
    return {
      userid: 64931,
      ordersLimit: 10, //拉取订单数量
      orderList: []
    }
  },
  components:{
      Header,
      NavBar
  },
  mounted(){
    this.getOrderList()
  },
  methods:{
    async getOrderList(){
      try {
        const result = await request.getOrderList(this.userid,this.ordersLimit)
        this.orderList = result
        console.log(result);
      } catch (error) {
        console.error(error);
      }
    }
  }
}
</script>

<style lang='less' scoped>

.page{
    padding-top: 1.95rem;
    position: relative;
    background-color: #f5f5f5;
    width: 414px;
    height: 100vh;
    .order{
      box-sizing: border-box;
      width: 100%;
      height: 6rem;
      margin-bottom: 0.5rem;
      background-color: #fff;
      padding: 0.3rem;
      display: flex;
      .left{
        width: 3rem;
        height: 100%;
        img{
          width: 2.5rem;
          height: 2.5rem;
        }
      }
      .right{
        position: relative;
        width: 100%;
        padding: 0 0.4rem;
        display: flex;
        flex-direction: column;
        .status{
          width: 100%;
          display: flex;
          justify-content: space-between;
          span{
            font-size: 0.65rem;
          }
          .title{
            font-size: 0.75rem;
            i{
              color: #999;
              font-size: 0.2rem;
            }
          }
          .time{
            padding: 0.1rem 0;
            color:#999;
            font-size: 0.55rem;
          }
        }
        .money{
          display: flex;
          justify-content: space-between;
          padding: 1rem 0;
          .title{
            color: #999;
            font-size: 0.6rem;
          }
          .price{
            color: #f60;
            font-size: 0.7rem;
            font-weight: bold;
          }
        }
        .more{
          position: absolute;
          right: 0.4rem;
          bottom: 0.5rem;
          button{
            font-size: .5rem;
            border: .025rem solid #3190e8;
            padding: 0.2rem;
            color: #3190e8;
            background-color: #fff;
          }
        }
      }
    }
  }
</style>
