  mounted() {
    this.telePhone();
  },
  methods: {
    telePhone() {
      const userInfo = JSON.parse(localStorage.getItem("userInfo") || "[]");
      this.userInfo = userInfo;
    },
    // 点击退出登录
    outMself() {
      // 清楚浏览器缓存数据
      localStorage.removeItem("userInfo");
      this.$router.push("/myself");
