<template>
  <div>
    <div class="footer">
      <div class="cartContainer" @click="showCart">
        <van-icon name="shopping-cart" class="cartIcon" />
      </div>
      <div class="footer_content">
        <span class="totalPrice">￥{{cartList[0]?totalPrice:'0'}}</span>
        <span class="fenPrice"
          >配送费￥0</span
        >
      </div>
      <div class="last" :class="{active:cartList.length}">
        <span class="song" @click="$router.push('/payment')">
          {{cartList.length?'点击结算':'暂无商品'}}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "shopNav",
  methods:{
    showCart(){
      this.$emit('showCart')
    }
  },
  props:{
    cartList:Array
  },
  computed:{
    // cartList(){
    //   return JSON.parse(localStorage.getItem('cartList')) || []
    // },
    totalPrice(){
      return this.cartList.reduce((sum,item)=>{
        sum += item.price*item.count
        return sum
      },0)
    }
  }
};
</script>

<style lang="less" scoped>
.footer {
  height: 2rem;
  background-color: #3d3d3f;
  position: fixed;
  bottom: 0;
  z-index: 999;
  width: 100%;
  .cartContainer {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-color: #3d3d3f;
    border: 0.2rem solid #555;
    position: absolute;
    left: 0.75rem;
    bottom: 0;
    &.active{
      background-color: yellowgreen;
    }
    .cartIcon {
      width: 100%;
      height: 100%;
      font-size: 1.3rem;
      color: white;
      text-align: center;
      line-height: 2rem;
    }
  }
  .footer_content {
    position: absolute;
    left: 4.5rem;
    width: 4rem;
    display: block;
    height: 1.2rem;
    .totalPrice {
      color: white;
      // font-weight: bold;
      margin: 0 0 0.1rem 0;
      font-size: small;
    }
    .fenPrice {
      color: white;
      font-size: 0.1rem;
      left: 0;
    }
  }
  .last {
    width: 5.85rem;
    height: 2.3rem;
    right: 0;
    color: white;
    position: absolute;
    background-color: #555;
    &.active{
      background-color: yellowgreen;
    }
    .song {
      font-weight: bold;
      font-size: 0.7rem;
      text-align: center;
      line-height: 2.3rem;
      position: absolute;
      left: 0.7rem;
    }
  }
}
</style>
