// 引入api
import request from '../../../api/index'
const state = {
    addressList:[]
}
const mutations = {
    GETADDRESS(state,addressList){
        state.addressList = addressList
    }
}
const actions = {
    async getAddress({commit},userid){
        try {
            const result = await request.getAddress(userid)
            commit('GETADDRESS',result)
        } catch (error) {
            console.error(error);
        }
    }
}
const getters = {

}
export default {
    state,mutations,actions,getters
}