<template>
  <div class="headerContainer">
    <div>
      <div class="header">
        <van-icon name="arrow-left" class="icon" @click="goback"/>
        <span>{{foodsdetail.name}}</span>
      </div>
      <!-- <img src="./images/tupian.png"> -->
      <!-- <img :src="`${foodsdetail.image_path}.jpg`"> -->
      <img :src="`https://elm.cangdu.org/img/${foodsdetail.image_path}`">
      <p class="content">{{foodsdetail.description}}</p>
      <div class="detail_container">
        <section class="detail_left">
          <p class="detail_content">{{foodsdetail.name}}</p>
          <div class="rating_sale">
            <p>评分</p>
            <div>
              <van-icon name="star" class="star"/>
              <van-icon name="star" class="star"/>
              <van-icon name="star" class="star"/>
              <van-icon name="star" class="star"/>
              <van-icon name="star-o" class="star-o"/>
            </div>
            <span>{{foodsdetail.rating}}</span>
          </div>
          <p class="price">
            <span class="price_one">月售{{foodsdetail.month_sales}}</span>
            <span class="price_two">售价￥{{foodsdetail.specfoods ? foodsdetail.specfoods[0].price: 0}}</span>
            <span class="price_three">起</span>
          </p>
          <p class="comment">
            <span>评论数 {{foodsdetail.rating_count}}</span>&nbsp;
            <span>好评率 {{foodsdetail.satisfy_rate}}%</span>
          </p>
        </section>
      </div>
      <!-- 底部 -->
      <shopNav @showCart="showPopup" :cartList='cartList'/>
      <van-popup closeable position="bottom" :style="{ height:'30%',padding:'1.4rem .2rem 0' }" v-model="show" >
        <van-cell v-for="(item,index) in cartList" :title="item.name" :value="'价格:¥'+item.price" :label="'数量:'+item.count" :key="index"/>
      </van-popup>
    </div>
  </div>
</template>

<script>
import shopNav from '../../components/shopNav'
export default {
  name: 'goodDetail',
  data() {
    return {
      show: false,
      cartList: JSON.parse(localStorage.getItem('cartList')) || []
    }
  },
  mounted(){
    this.$store.dispatch('getGoodDetail',1)
    console.log(this.foodsdetail)
  },
  components:{
    shopNav
  },
  computed:{
    // ...mapGetters(['foodsdetail'])
    goodIndex(){
      return this.$route.query.id*1
    },
    curindex(){
      return this.$route.query.curindex*1
    },
    foodsdetail(){
      return this.$store.state.goodDetail.detailList[this.curindex]?this.$store.state.goodDetail.detailList[this.curindex].foods[this.goodIndex]:{}
    }
    // ...mapState({foodsdetail:(state)=>state.goodDetail.detailList[0]?state.goodDetail.detailList[0].foods[0]:{}}),
  },
  methods:{
    goback(){
      this.$router.back()
    },
    showPopup() {
      console.log(111);
      this.show = true;
    },
  }
}
</script>


