// 引入axios
import reqaxios from "../axios";
// https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762
// 获取商铺列表
export const  getrestaurantsList = (classifyItem) => 
// reqaxios({url:`/shopping/restaurants?latitude=${classifyItem.latitude}&longitude=${classifyItem.longitude}&order_by=${classifyItem.order_by}`,method:'get'})
reqaxios({url:`/shopping/restaurants`,method:'get',params:classifyItem});
// https://elm.cangdu.org/shopping/v2/restaurant/category  
// 获取下拉商铺分类列表
export const  getFoodData = () => reqaxios({url:`/shopping/v2/restaurant/category`,method:'get'});
// https://elm.cangdu.org/shopping/v1/restaurants/activity_attributes
// 商家属性列表
export const  getRestaurantsAttrList = () => reqaxios({url:`/shopping/v1/restaurants/activity_attributes`,method:'get'});

// https://elm.cangdu.org/shopping/v1/restaurants/delivery_modes
// 获取配送方式 
export const  getDeliveryModes = () => reqaxios({url:`/shopping/v1/restaurants/delivery_modes`,method:'get'})
// https://elm.cangdu.org/shopping/v2/restaurant/category
// 获取所有商铺分类列表
export const  getCategoryList = () => reqaxios({url:`/shopping/v2/restaurant/category`,method:'get'})