//引入路由组件
// 我的订单
import Order from '@/pages/order/order' 
// 我的
import Myself from '@/pages/myself/myself'
// 下载APP
import Download from '@/pages/download/download'
// 积分
import Recordone from '@/pages/recordone/recordone'
// 余额
import Recordtwo from '@/pages/recordtwo/recordtwo'
// 服务中心
import Service from '@/pages/service/service'
// 积分商城
import Integral from '@/pages/integral/integral'
// 会员
import Vip from '@/pages/vip/vip'
// 食物商家分类
import Foodsorts from '@/pages/Foodsorts'
// 选择地址
import chooseAddress from '@/pages/chooseAddress'
// 增加地址
import addAddress from '@/pages/addAddress'
//引入首页
import Home from '@/pages/Home/Home'
//引入在线支付
import Payment from '@/pages/Payment/Payment'
//引入搜索
import Search from '@/pages/search/index'
//引入订单页
import Orders from  '@/pages/orders/index'
// 登陆页面
import loginAndRegister from '@/pages/loginAndRegister'
//商品详情页
import Goodeatil from '@/pages/goodDetail'

// //商品详情页
// import Buiness from '@/pages/Buiness'
// //商品详情页
// import Confirm from '@/pages/Confirm'
// 商店详情页
import Shop from '@/pages/Buiness'
// 退出登录页面
import Signout from '@/pages/signout/signout'



export const routes = [
    {
        path: '/myself',
        component: Myself,
        children: [
            {
                path: '/order',
                component: Order
            },
            {
                path: '/download',
                component: Download
            },
            {
                path: '/recordone',
                component: Recordone
            },
            {
                path: '/recordtwo',
                component: Recordtwo
            },
            {
                path: '/service',
                component: Service
            },
            {
                path: '/integral',
                component: Integral
            },
            {
                path: '/vip',
                component: Vip
            },
        ]
    },
    {
        path: '/Gooddetatil',
        component: Goodeatil
    },
    {
        path: '/signout',
        component: Signout
    },
    {
        path: '/shop',
        component: Shop
    },
    {
        path: '/search',
        component: Search
    },
    {
        path: '/orders',
        component: Orders
    },
    
    {
        path: '/payment',
        component: Payment
    },
    {
        path: '/food',
        component: Foodsorts
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/chooseAddress',
        //重定向路由
        component: chooseAddress
    },
    {
        path: '/addAddress',
        //重定向路由
        component: addAddress
    },
    {

        path: '/login',
        component: loginAndRegister
    },
    {
        path: '/',
        //重定向路由
        redirect: '/home'
    },
]