// 引入api
import aaa from '../../../api/index'

const state = {
    code: ''
}
const mutations = {
    GETVCODE(state, code) {
        state.code = code
    }
}
const actions = {
    async getVcode({ commit }) {
        const result = await aaa.getVcode()
        console.log(result)
        commit('GETVCODE', result.code)
    }
}
const getters = {

}
export default {
    state, mutations, actions, getters
}