// 引入api
import { getHomeList } from "../../../api/home/home";
import { getHomeShopList } from "@/api/home/home";
const state = {
  homeList: [],
  shopList:[]
};
const mutations = {
  GETHOMELIST(state, home) {
    state.homeList = home;
  },
  GETHOMESHOPLIST(state,shop){
      state.shopList = shop
  }
};
const actions = {
    // 首页轮播图数据
  async getHomeList({ commit }) {
    const result = await getHomeList();
    try {
      commit("GETHOMELIST", result);
    } catch (error) {
      console.error(error);
    }
  },
  //获取商家列表数据
  async getHomeShopList({commit}){
      const result = await getHomeShopList()
      try {
          commit('GETHOMESHOPLIST',result)
      } catch (error) {
        console.error(error);
      }
  }
};
const getters = {};
export default {
  state,
  mutations,
  actions,
  getters,
};
