import Vue from 'vue'
import App from './App.vue'
// 引入路由
import router from '@/router/index'
// 引入vuex
import store from './store/index.js'
// 全部引入vant
// import Vant from 'vant';
// import 'vant/lib/index.css';
// Vue.use(Vant);


// 按需引入vant组件
import { DropdownMenu, DropdownItem ,Icon,Button,TreeSelect,Tag,Grid,GridItem} from 'vant';
Vue.use(TreeSelect);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Tag);
Vue.use(Grid);
Vue.use(GridItem);
//路遥引入
import 'vant/lib/index.css'
import {Swipe, SwipeItem,Sticky,RadioGroup, Radio,Cell,Popup,CountDown,Toast} from 'vant'
Vue.use(Popup);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Cell)
Vue.use(Sticky)
Vue.use(Swipe)
Vue.use(SwipeItem)
Vue.use(CountDown)
Vue.use(Toast)
//ljyyinru

//引入rem适配
import './config/rem/rem'
Vue.config.productionTip = false

new Vue({
 
  render: h => h(App),
  router,
  store
}).$mount('#app')
