<template>
  <div class="foodCantainer">
    <div class="header">
      <van-icon name="arrow-left" @click="$router.back()"/>
      <!-- <i class="el-icon-arrow-left"></i> -->
      <div class="title">{{title}}</div>
    </div>
    <!-- 导航条 -->
    <div class="navbar">
      <van-dropdown-menu active-color="#1989fa">
        <!-- 分类 -->
        <van-dropdown-item :title="title" ref="classify">
          <van-tree-select
            :items="items"
            :active-id.sync="activeId"
            :main-active-index.sync="activeIndex"
            @click-item = 'onClickItem'
          >>
          </van-tree-select>
        </van-dropdown-item>
        <!-- 排序 -->

        <van-dropdown-item title="排序" :options="option2" v-model="value" @change="changeOrderBy(value)"/>
        <!-- 筛选 -->
        <van-dropdown-item title="筛选" ref="screen">
          <div class="screen" style="margin-top:.5rem">
            <div class="title" style="font-size:.6rem ;margin-left:.5rem">
              配送方式
            </div>

            <button
              class="btn"
              v-for="item in deliveryModesList"
              :key="item.id"
              style="width:25%; padding:.4rem 0; background:none; border: none;border: .05rem solid rgb(209, 196, 196); margin:.5rem ;font-size:.6rem"

            >
              {{ item.text }}
            </button>
            <div class="title" style="font-size:.6rem;margin-left:.5rem">
              商家属性,可以多选(可以多选)
            </div>
            
            <!-- 属性列表 -->
            <div style="display:flex;flex-wrap: wrap;width:100% ">
              <div
                v-for="(item,index) in restaurantsAttrList"
                :key="item.id"
                style="width:30%;position:relative; margin-right:.4rem "

              >

                <button
                  :data-id='index'
                  style="width:100%;position:relative; padding:.4rem 0; background:none; border: none;border: .05rem solid rgb(209, 196, 196);
                   margin:.5rem ; font-size:.6rem" 
                   @click="chooseAttr($event,item,index)"
                >
                  <div v-if="!item.isChoose"
                    :style="
                      `color:#${item.icon_color}; position:absolute;top:0.38rem;left:0.28rem; width:.7rem;height:.7rem;border:.05rem solid #${item.icon_color} ;font-size:.6rem`
                    "
                  >
                    {{ item.icon_name }}
                  </div>
                  <!-- 选中图标 -->
                  <van-icon name="success" v-else/> 
                  {{ item.name }}
                </button>
              </div>
            </div>

            <div>
              <van-button type="default" style="width:50%" @click="onClear">清空</van-button>
              <van-button type="primary" style="width:50%" @click="onConfirm"
                >确认({{attrList.length}})</van-button
              >
            </div>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <!-- 主菜单 -->

    <Restaurants :restaurantsList="restaurantsList" ></Restaurants>
  </div>
</template>

<script>
import Restaurants from "@/components/Restaurants";
import { mapState } from "vuex";
export default {
  name: "Foodsorts",
  components: { Restaurants },
  computed: {
    //  foodData:[], //食物分类列表
    // restaurantsList:[],//商家列表
    // restaurantsAttrList :[],//s商家属性
    // deliveryModesList :[],//配送方式
    ...mapState({ foodData: (state) => state.food.foodData }),
    ...mapState({ restaurantsList: (state) => state.food.restaurantsList }),
    ...mapState({
      restaurantsAttrList: (state) => state.food.restaurantsAttrList,
    }),
    ...mapState({ deliveryModesList: (state) => state.food.deliveryModesList }),
    ...mapState({ categoryList: (state) => state.food.categoryList }),
    items() {
      return this.categoryList.map((e) => ({
        text: e.name,
         dot:true,
        badge: e.count,       
        id:e.id,
        // dot:false,
        children: e.sub_categories.map((i) => ({ text: i.name, id: i.id })),
      }));
    }

  },
  components: { Restaurants },
  beforeMount() {
    this.$store.dispatch("getDeliveryModes");
    this.getrestaurants(this.classifyItem);
    
    this.$store.dispatch("getFoodData");
  
    this.$store.dispatch("getRestaurantsAttrList");
    this.$store.dispatch("getCategoryList");
  },
  mounted() {
  
    this.classifyItem.restaurant_category_id = this.$route.query.id
     
  },
 
  data() {
    return {
      title:this.$route.query.title,
      attrList:[],
      // item 为分类显示所需的数据，数据格式见下方示例。main-active-index 表示左侧高亮选项的索引，active-id 表示右侧高亮选项的 id。
      // 分类中的数据
      classifyItem: {
        latitude: 113.76077,
        longitude: 34.73822,
        offset: 0, //跳过几条数据
        limit: 20, //请求数据条数
        restaurant_category_id: '', //餐馆分类id
        order_by: 1, //排序方式id(1~6)
        delivery_mode: [], //配送方式id数组
        restaurant_category_ids: [], //餐馆分类id数组
        support_ids: [], //餐馆支持特权的id数组
      },
      children:[],
      activeId: 1, //选中项的id
      activeIndex: 0, //选中的索引
      value: 0,
      option2: [
        { text: "排序", value: 0 },
        { text: "智能排序", value: 1 },
        { text: "距离最近", value: 2 },
        { text: "销量最高", value: 3 },
        { text: "起送价最低", value: 4 },
        { text: "配送速度最快", value: 5 },
        { text: "评分最高", value: 6 },
      ],
    };
  },
  methods: {
    getrestaurants(classifyItem){
        this.$store.dispatch("getrestaurantsList",classifyItem );

    },
    // goBack() {
    //   console.log("回");

    // },
    
    
    //点击分类的子项目
    onClickItem(data){
      // console.log(data);
      this.title = data.text
      this.classifyItem.restaurant_category_ids.push(data.id)
      this.getrestaurants(this.classifyItem)
      this.classifyItem.restaurant_category_ids=[]
    },
    // 排序
    changeOrderBy(value){
      // console.log(value)
      this.value = value
      // let msg =this.option2.find(item=>item.value===this.value).text
      // // console.log(msg,value)
      // 销量
     if(this.value==3){//销量最高
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return b.recent_order_num  - a.recent_order_num 
       })
     }else if(this.value==6){//评分最高
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return b.rating  - a.rating
       })
     }else if(this.value==4){//起送价最低
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return a.float_minimum_order_amount - b.float_minimum_order_amount  
       })
     }
    },
    // 点击选择属性
    chooseAttr(event,item,index){
     
      item.isChoose = true
      
      // 将属性添加到data的属性列表中
      if(!this.attrList.find(i=>i.name==item.name)){
        this.attrList.push(item)
      }
      
    },
    // 清空属性
    onClear(){
      // 改变选中状态
    
      this.$store.state.food.restaurantsAttrList.map(item=>{
        if(item.isChoose){
          item.isChoose=false
        }
        return
      })
      // 清空存储的属性
      this.attrList = []
    },
    // 确认筛选属性
    onConfirm() {
      // let newArr = []
      // console.log(this.attrList);
      // let attrList = [].slice.call(this.attrList)
        // console.log(attr)
        //  console.log(typeof attr)
     this.$store.state.food.restaurantsList = this.$store.state.food.restaurantsList.filter(i=>{
      return i.supports.some(every=>{
          return this.attrList.some(e=>e.name==every.name);
        })
      })
      this.attrList = []
      this.$refs.screen.toggle();
    },
    


    //点击分类的子项目
    onClickItem(data){
      // console.log(data);
      this.title = data.text
      this.classifyItem.restaurant_category_ids.push(data.id)
      this.getrestaurants(this.classifyItem)
      this.classifyItem.restaurant_category_ids=[]
    },
    // 排序
    changeOrderBy(value){
      // console.log(value)
      this.value = value
      // let msg =this.option2.find(item=>item.value===this.value).text
      // // console.log(msg,value)
      // 销量
     if(this.value==3){//销量最高
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return b.recent_order_num  - a.recent_order_num 
       })
     }else if(this.value==6){//评分最高
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return b.rating  - a.rating
       })
     }else if(this.value==4){//起送价最低
       this.$store.state.food.restaurantsList.sort(function(a,b){
        // console.log(a.recent_order_num,b.recent_order_num)
          return a.float_minimum_order_amount - b.float_minimum_order_amount  
       })
     }
    },
    

  },

};
</script>

<style lang="less" scoped>
.foodCantainer {
  width: 100%;
  padding-top: 2rem;

  // 头部
  .header {
    width: 100%;
    display: flex;

    background-color: rgb(49, 144, 232);
    height: 1.95rem;

    i {
      font-size: 1.3rem;
      line-height: 1.95rem;
      flex: 1;
      color: #fff;
    }
    .title {
      // text-align: center;
      flex: 1;
      line-height: 1.95rem;
      color: #fff;
      font-weight: 700;
    }
  }
  // 导航条
  .navBar {
    position: fixed;
    top: 2rem;
    display: flex;
    width: 100%;
    height: 1.875rem;
    border-bottom: 0.05rem solid rgb(241, 235, 235);
  }
  // 内容展示区
  /deep/.van-tree-select__nav {
    width: 8.5rem;
  }
}
</style>