import request from "../../../api/index";
// 引入api

const state = {
    shopdetail:{},
    foodList:[]
}
const mutations = {
    GETSHOPDETAIL(state,shopdetail){
        state.shopdetail = shopdetail
    },
    GETFOODLIST(state,foodList){
        state.foodList = foodList
    },
}
const actions = {
    async getBuinessList ({commit},id){
        let result = await request.getShopdetail(id)
        commit('GETSHOPDETAIL',result)
    },
    async getFoodList ({commit},id){
        let result = await request.getFoodList(id)
        commit('GETFOODLIST',result)
    },



}
const getters = {

}
export default {
    state,mutations,actions,getters
}