<template>
  <div class="page">
      <!-- 头部导航 -->
      <Header title="选择收货地址" :func="editOrDone?'编辑':'完成'" @update:func="changeType"/>
      <!-- 地址列表 -->
      <ul class="list">
          <li class="item" v-for="(address,index) in addressList" :key="index">
              <div class="check">
                  <i class="iconfont icon-check1" v-show="index===0"></i>
              </div>
              <div class="info">
                  <div class="user">
                      <span class="username">{{address.name}}</span>
                      <span class="gender">{{address.sex===1?'先生':'女士'}}</span>
                      <span class="tele">{{address.phone}}</span>
                  </div>
                  <div class="address">
                      <div 
                      :class="{other:address.tag_type===1,
                        home:address.tag_type===2,
                        school:address.tag_type===3,
                        company:address.tag_type===4}"
                       class="tag">{{address.tag}}</div>
                      <div class="addressName">{{address.address_detail}}</div>
                  </div>
              </div>
              <i @click="deleteAddress(index)" v-show="!editOrDone" class="delete iconfont icon-069delete">删除</i>
          </li>
      </ul>
      <!-- 新增收货地址 -->
      <div class="addAddress">
          <i class="iconfont icon-plussign" @click="toAddAddress">新增收货地址</i>
      </div>
  </div>
</template>

<script>
//引入发送请求接口
import request from '../../api/index'
import { mapState } from 'vuex'
//引入头部跳转组件
import Header from '../../components/Header'
export default {
  name: 'chooseAddress',
  data() {
      return {
          userid: 64931,
          editOrDone:true,
          addressid:'',
      }
  },
  components:{
    Header
  },
  computed:{
      ...mapState({
          addressList: state => state.address.addressList
      }),
  },
  mounted(){
      this.getAddress()
  },
  methods:{
      getAddress(){
          this.$store.dispatch('getAddress',this.userid)
      },
      //跳转到添加地址页面
      toAddAddress(){
          this.$router.push({path:'addAddress'})
      },
      changeType(type){
          if(type==='编辑'){
              this.editOrDone = false
          }else{
              this.editOrDone= true
          }
      },
      //删除地址
      async deleteAddress(index){
        const adressid = this.addressList[index].id
        try {
            const result= await request.deleteAddress(this.userid,adressid)
            if(result.status===1){
                console.log(result)
                this.getAddress()
            }
        } catch (error) {
            console.error(error);
        }
      }
  }
}
</script>

<style lang='less' scoped>
.page{
    background-color: #f5f5f5;
    width: 100%;
    height: 100vh;
    padding-top: 1.95rem;
    box-sizing: border-box;
    .list{
        width: 100%;
        .item{
            border-bottom: 0.01rem solid grey;
            position: relative;
            background-color: #fbfbfb;
            width: 100%;
            height: 4rem;
            display: flex;
            .check{
                width: 3rem;
                display: flex;
                justify-content: center;
                align-items: center;
                i{
                    font-size: 1.2rem;
                    color: #4cd964;
                    font-weight: bold;
                }
            }
            .info{
                display: flex;
                flex-direction: column;
                justify-content: center;
                div{
                    padding: .1rem;
                    height: 1.3rem;
                    line-height: 1.3rem;
                    color: #777;
                    font-size: .65rem;
                    &.user{
                        display: flex;
                        span{
                            margin-right: .2rem;
                        }
                        .username{
                            font-size: .9rem;
                            color: #333;
                            font-weight: bold;
                        }
                    }
                    &.address{
                        padding: .1rem;
                        display: flex;
                        align-items: center;
                        div.home{
                            background-color: #ff5722;
                        }
                        div.company{
                            background-color: rgb(76, 217, 100);
                        }
                        div.school{
                            background-color: rgb(49, 144, 232);
                        }
                        div.other{
                            background-color: rgb(134, 207, 144);
                        }
                        .tag{
                            margin-right: .2rem;
                            height: .5rem;
                            font-size: .5rem;
                            line-height: .5rem;
                            border-radius: .1rem;
                            background-color:#4cd964;
                            color: #fbfbfb;
                        }
                    }
                }
            }
            .delete{
                font-size: .75rem;
                color: red;
                position: absolute;
                right: 1rem;
                top: 1.2rem;
            }
        }
    }
    .addAddress{
        margin-top: 1rem;
        width: 100%;
        text-align: center;
        i{
            color: #3190e8;
            font-size: 0.7rem;
        }
    }
}
</style>
