<template>
  <div class="NavBar">
      <div>
        <i @click="goHome" data-type="1" class="iconfont icon-eleme" :class="{active:index===1}"></i>
        <span>外卖</span>
      </div>
      <div>
        <i @click="goSearch" data-type="2" class="iconfont icon-zhinanzhen" :class="{active:index===2}"></i>
        <span>搜索</span>
      </div>
      <div>
        <i @click="goOrder" data-type="3" class="iconfont icon-dingdan" :class="{active:index===3}"></i>
        <span>订单</span>
      </div>
      <div>
        <i @click="goMyself" data-type="4" class="iconfont icon-yonghu" :class="{active:index===4}"></i>
        <span>用户</span>
      </div>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  data() {
    return {
      // index: his.$route.path==='/home'?1:this.$route.path==='/search'?2:this.$route.path==='/orders'?3:4
    }
  },
  computed:{
    index(){
      return this.$route.path==='/home'?1:this.$route.path==='/search'?2:this.$route.path==='/orders'?3:4
    }
  },
  methods: {
    goHome(event){
      // sessionStorage.setItem('NavIndex',JSON.stringify(event.target.dataset.type*1))
      this.$router.push('/home')
    },
    goSearch(event){
      // sessionStorage.setItem('NavIndex',JSON.stringify(event.target.dataset.type*1))
      this.$router.push('/search')
    },
    goOrder(event){
      // sessionStorage.setItem('NavIndex',JSON.stringify(event.target.dataset.type*1))
      this.$router.push('/orders')
    },
    goMyself(event){
      // sessionStorage.setItem('NavIndex',JSON.stringify(event.target.dataset.type*1))
      this.$router.push('/myself')
    }
  },
}
</script>

<style lang='less' scoped>
.NavBar{
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    z-index: 100;
    width: 100vw;
    height: 1.95rem;
    border-top: 2px solid #f1f1f1;
    background-color: #fbfbfb;
    div{
      width: 25%;
      display: flex;
      flex-direction: column;
      align-items: center;
      i{
          margin: auto;
          font-size: 1rem;
          color: #777;
          &.active{
              color:#3190e8;
          }
      }
      span{
        font-size: 0.4rem;
      }
    }
}
</style>
