<template>
  <div class="page">
    <!-- 头部 -->
    <Header :title="shopdetail.name" ></Header>
    <!-- 中间 -->
    <div class="swiper-container">
      <div class="shopHeader">
        <img :src="`https://elm.cangdu.org/img/${shopdetail.image_path}`">
        <div class="info">
          <p class="name">{{shopdetail.name}}</p>
          <p class="way">{{shopdetail.description}}</p>
          <p class="anouce">{{shopdetail.address}}</p>
        </div>
      </div>
    </div>
    <div class="tab">
      <div class="left">
        <span class="active">商品</span>
      </div>
      <div class="left">
        <span>评价</span>
      </div>
    </div>
    <div class="goodDtail">
      <div class="category">
        <div class="name" @click="changeIndex(index)" :class="{active:curindex===index}" v-for="(food,index) in foodList" :key="index">{{food.description}}</div>
      </div>
      <div class="goods">
        <div class="hot">{{food.name}}</div>
        <div class="good" v-for='(item,index) in food.foods' :key="index">
          <img :src="`https://elm.cangdu.org/img/${item.image_path}`">
          <div class="goodinfo" @click="toDetail(index)">
            <p class="name">{{item.name}}</p>
            <p class="way">¥{{item.specfoods[0].price}}</p>
            <p class="anouce">{{`月售${item.month_sales}份 好评率${item.satisfy_rate}%`}}</p>
          </div>
          <i class="iconfont icon-plussign" @click="addGoods(item.specfoods[0].name,item.specfoods[0].price)"></i>
        </div>
      </div>
    </div>
    <!-- 底部 -->
    <shopNav @showCart="showPopup" :cartList='cartList'/>
    <van-popup closeable position="bottom" :style="{ height:'30%',padding:'1.4rem .2rem 0' }" v-model="show" >
      <van-cell v-for="(item,index) in cartList" :title="item.name" :value="'价格:¥'+item.price" :label="'数量:'+item.count" :key="index"/>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Header from '../../components/Header'
import shopNav from '../../components/shopNav'
export default {
  name: "Buiness",
  data() {
    return {
      curindex: 0,
      show: false,
      cartList: JSON.parse(localStorage.getItem('cartList')) || []
    }
  },
  components:{
    Header,
    shopNav
  },
  mounted() {
    this.getBuinessList();
    this.getFoodList();
  },
  computed:{
    ...mapState({
          shopdetail:state=> state.buiness? state.buiness.shopdetail:{},
          foodList:state=> state.buiness?state.buiness.foodList.slice(0,10):[]
      }),
      food(){
        return this.foodList[this.curindex] || []
      },
      // cartList(){
      //   return JSON.parse(localStorage.getItem('cartList')) || []
      // },
      //店铺id
      id(){
        return this.$route.query.id
      }
  },
  methods: {
    getBuinessList() {
      this.$store.dispatch("getBuinessList",this.id);
    },
    getFoodList() {
      this.$store.dispatch("getFoodList",this.id);
    },
    changeIndex(index){
      this.curindex = index
    },
    toDetail(index){
      this.$router.push(`/gooddetatil?id=${index}&curindex=${this.curindex}`)
    },
    addGoods(name,price){
      //创建一个对象
      const food = {
        name,
        price,
        count: 1
      }
      // 从本地取出,没有新建
      const cartList = JSON.parse(localStorage.getItem('cartList')) || []
      //如果原来有就数量加1
      const repeat = cartList.find(item=>item.name===food.name)
      if(repeat){
        cartList.forEach(item => {
          if(item.name === repeat.name){
            item.count += 1
          }
        });
      }else{
        //加入原来的数组
        cartList.push(food)
      }
      this.cartList = cartList
      localStorage.setItem('cartList',JSON.stringify(cartList))
      console.log(111);
      this.$toast(`${name}加入购物车成功`);
    },
    showPopup() {
      this.show = true;
    },
  }
};
</script>

<style lang='less' scoped>
.page{
    position: relative;
    padding-top: 6.95rem;
    position: relative;
    background-color: #f5f5f5;
    width: 414px;
    .swiper-container{
      position: fixed;
      width: 100%;
      top: 1.95rem;
      background-color: deepskyblue;
      .shopHeader{
        
        width: 100%;
        height: 3rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        img{
          width: 2.5rem;
          height: 2.5rem;
        }
        .info{
          .name{
            font-weight: bold;
            font-size: .6rem;
          }
          font-size: .4rem;
          width: 10rem;
          color: #fff;
        }
      }

    }
    .tab{
      position: fixed;
      width: 100%;
      top: 4.95rem;
      display: flex;
      background-color: #fff;
      div{
        width: 50%;
        text-align: center;
        height: 2rem;
        line-height: 2rem;
        &>span.active{
          padding: .2rem;
          color: blue;
          border-bottom: 1px solid blue;
        }
      }
    }
    .goodDtail{
      display: flex;
      justify-content: space-between;
      .category{
        height: 70vh;
        overflow: scroll;
        .name{
          font-size: .6rem;
          text-align: center;
          width: 3.5rem;
          height: 2.5rem;
          line-height: 2.5rem;
          margin-left: .1rem;
          overflow: hidden;
          white-space: nowrap;
        }
        &>div.active{
          border-left: 2px solid blue;
          background-color: #fff;
        }
      }
      .goods{
        height: 70vh;
        overflow: scroll;
          display: flex;
          flex-direction: column;
        width: 12rem;
        div.hot{
          color: #777;
          font-weight: bold;
        }
        div.good{
          box-sizing: border-box;
          padding: .3rem;
          background-color: #fff;
          width: 100%;
          height: 3rem;
          display: flex;
          align-items: center;
          justify-content: space-evenly;
          img{
            width: 2.5rem;
            height: 2.5rem;
          }
          div.goodinfo{
            p{
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .name{
              font-weight: bold;
              font-size: .8rem;
            }
            width: 6rem;
            font-size: .6rem;
            color: #777;
          }
          i{
            font-weight: bold;
            color: blue;
          }
        }
      }
    }

}
</style>
