<template>
  <div class="page">
    <!-- 头部 -->
    <Header title="搜索" />
    <!-- 搜索框 -->
    <div class="inputBox">
      <input
        type="text"
        placeholder="请输入商家或美食名称"
        v-model="submitList"
      />
      <button class="commit" @click="Submit">提交</button>
    </div>
    <!-- 搜索历史区域 -->
    <div class="history">
      <!-- 搜索标题 -->
      <div class="title">搜索历史</div>
      <!-- 搜索历史列表 -->
      <ul class="list" v-for="(item, index) in mit" :key="index">
        <li v-if="show">{{ item }}<i @click="deletOne(index)">x</i></li>
      </ul>
      <div class="clear" @click="delet">清空搜索历史</div>
    </div>
    <NavBar></NavBar>
  </div>
</template>

<script>
import Header from "../../components/Header";
import NavBar from "@/components/NavBar";
export default {
  name: "search",
  components: {
    Header,
    NavBar,
  },
  data() {
    return {
      submitList: "",
      mit: [],
      show: true,
    };
  },
  mounted() {
    //加载的时候存储
    if (this.mit === null) {
      this.mit = [];
    }
    var lastname = JSON.parse(localStorage.getItem("mit"));
    this.mit = lastname;
  },
  methods: {
    Submit() {
      if (this.mit === null) {
        this.mit = [];
      }
      if (this.submitList !== "") {
        this.mit.push(this.submitList);
        this.submitList = "";
        this.show = true;
        this.storage();
      }
    },
    delet(event) {
      this.show = false;
      this.mit = [];
      localStorage.removeItem("mit");
    },
    deletOne(index) {
      this.mit.splice(index, 1);
    },
    storage() {
      if (this.mit) {
        localStorage.setItem("mit", JSON.stringify(this.mit));
      }
    }
  },
};
</script>

<style lang='less' scoped>
.page {
  padding-top: 1.95rem;
  position: relative;
  background-color: #f5f5f5;
  width: 100vw;
  height: 100vh;
  .inputBox {
    width: 100%;
    height: 2.5rem;
    background-color: #fff;
    display: flex;
    justify-content: center;
    padding: 0.4rem;
    box-sizing: border-box;
    input {
      padding: 0.3rem;
      font-size: 0.65rem;
      width: 70%;
      outline: none;
      background-color: #f2f2f2;
      border: 1px solid #e4e4e4;
    }
    button.commit {
      font-weight: bold;
      margin-left: 0.2rem;
      width: 3rem;
      border: none;
      background-color: #3190e8;
      color: #fbfbfb;
      font-size: 0.65rem;
    }
  }
  .history {
    .title {
      box-sizing: border-box;
      padding: 0 0.5rem;
      width: 100%;
      height: 1.8rem;
      line-height: 1.8rem;
      font-weight: bold;
      color: #666;
      font-size: 0.65rem;
    }
    .list {
      li {
        position: relative;
        border-bottom: 0.025rem solid #e4e4e4;
        font-size: 0.7rem;
        height: 1.8rem;
        line-height: 1.8rem;
        padding: 0 0.5rem;
        background-color: #fff;
        i {
          position: absolute;
          right: 0.8rem;
          font-weight: bold;
          color: #777;
        }
      }
    }
    .clear {
      font-size: 0.7rem;
      width: 100%;
      height: 1.8rem;
      line-height: 1.8rem;
      text-align: center;
      background-color: #fff;
      color: #3190e8;
      font-weight: bold;
    }
  }
}
</style>
