<template>
  <div class="page">
    <!-- 头部 -->
    <Header title="密码登录" />
    <!-- 表单 -->
    <form class="loginAndRegister">
      <input class="account" type="text" placeholder="账号" v-model="phone" />
      <input
        class="password"
        type="text"
        placeholder="密码"
        v-model="password"
      />
      <div class="VerificationCode">
        <input
          class="verification"
          type="text"
          placeholder="验证码"
          v-model="codeNumber"
        />
       
         <div class="photo"><img :src="code" alt="" /></div>
        <!-- <div class="photo"><img :src="code" alt="" /></div> -->
        <span class="send" @click="getVcode()">换一张</span>
      </div>
    </form>
    <!-- 登录提示 -->
    <p>温馨提示：未注册过的账号，登录时将自动注册</p>
    <p>注册过的用户可凭账号密码登录</p>
    <!-- 登录按钮 -->
    <div class="button">
      <button @click="loginMyself">登录</button>
    </div>
    <!-- 重置密码 -->
    <a class="reset">重置密码?</a>
  </div>
</template>

<script>
// 引入请求接口
import request from "../../api/index";
import { mapState } from "vuex";
import Header from "../../components/Header";
// import axios from "axios";
export default {
  name: "loginAndRegister",
  data() {
    return {
      phone: "18683257353", //手机号
      password: "111111", //密码
      codeNumber: null, //验证码
      userInfo: {}, //获取的用户信息
      // code1:''
    };
  },
  components: {
    Header
  },
  computed: {
    ...mapState({
      code: (state) => state.login.code,
    }),
  },
  mounted() {
    this.getVcode();
  },
  methods: {
    async getVcode() {
     this.$store.dispatch("getVcode");
    //  const result = await axios.post('http://localhost:8001/v1/captchas')
     // console.log(result)
    //  this.code1 = result.data.code

    },
    // 页面登录
    async loginMyself() {
      const { phone, password, codeNumber } = this;
      // 手机号判断
      let num = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (!num.test(phone)) {
        alert("请输入正确的手机号");
        return;
      }
      // 密码判断
      if (!password) {
        alert("密码不能为空");
        return;
      }
      // 验证码判断是否为4位
      let codeNum = /^\d{4}$/;
      if (!codeNum.test(codeNumber)) {
        alert("验证码必须四位");
        return;
      }
      // 获取用户名和密码
      // 派发接口
      const params = { username: phone, password, captcha_code: codeNumber };

      try {
        let result = await request.userImagess(params);
        // let result = await axios({url:'http://localhost:8001/v2/login',method:'post',params});
        if (result.username) {
          localStorage.setItem("userInfo", JSON.stringify(result));
        }
      } catch (error) {
        console.error(error);
      }
      // this.$store.dispatch("userLogin", params);
      // 跳转页面
      const userInfo = JSON.parse(localStorage.getItem("userInfo"));
      if (userInfo) {
        this.$router.push("/myself");
      }
      console.log(userInfo);
    },
  },
};
</script>

<style lang="less" scoped>
.page {
  padding: 1.95rem 0;
  position: relative;
  background-color: #f5f5f5;
  width: 100vw;
  height: calc(100vh-1.95rem * 2);
  .loginAndRegister {
    width: 100%;
    margin-top: 1rem;
    input {
      font-size: 0.7rem;
      color: #777;
      height: 1.8rem;
      text-indent: 1rem;
      width: 100%;
      border: none;
      outline: none;
      border-bottom: 1px solid #f1f1f1;
    }
    .VerificationCode {
      background-color: #fff;
      font-size: 0.6rem;
      height: 2rem;
      .verification {
        width: 60%;
      }
      .photo {
        display: inline;
        margin-top: -1rem;
      }
      .send {
        margin-left: 0.5rem;
        color: #3b95e9;
      }
    }
  }
  p {
    font-size: 0.5rem;
    color: red;
    padding: 0.4rem 0.6rem;
    line-height: 0.5rem;
  }
  .button {
    display: flex;
    justify-content: center;
    button {
      width: 95%;
      font-size: 0.7rem;
      color: #fff;
      background-color: #4cd964;
      padding: 0.5rem 0;
      border: none;
      border-radius: 0.15rem;
      text-align: center;
    }
  }
  .reset {
    position: absolute;
    right: 0.5rem;
    margin-top: 1rem;
    color: #3b95e9;
    font-size: 0.6rem;
  }
}
</style>
