<template>
  <div id="app">
    <div class="home">
      <div class="header">
        <div class="head">
          <van-icon name="search" @click="$router.replace('/search')"/>
          <h4>昌平区北七家宏福...</h4>
          <h6 @click="goLogin" v-if="!isLogin">登录 | 注册</h6>
          <h6 v-else>{{isLogin.city}}</h6>
        </div>
      </div>
      <div class="footer">
        <van-swipe class="my-swipe">
          <van-swipe-item class="menu">
            <div class="menuStyle">
              <div
                class="menuList"
                v-for="item in oneArr"
                :key="item.id"


                @click="goFood(item.id,item.title)"

              >
                <img :src="htp + item.image_url" />
                <p>{{ item.title }}</p>
              </div>
            </div>
            <div class="menuStyle">
              <div
                class="menuList"
                v-for="item in twoArr"
                :key="item.id"

                @click="goFood(item.id,item.title)"
              >
                <img :src="htp + item.image_url" />
                <p>{{ item.title }}</p>
              </div>
            </div>
          </van-swipe-item>
          <van-swipe-item class="menu">
            <div class="menuStyle">
              <div
                class="menuList"
                v-for="item in threeArr"
                :key="item.id"
                @click="goFood(item.id,item.title)"
              >
                <img :src="htp + item.image_url" />
                <p>{{ item.title }}</p>
              </div>
            </div>
            <div class="menuStyle">
              <div
                class="menuList"
                v-for="item in fourArr"
                :key="item.id"
               @click="goFood(item.id,item.title)"
              >
                <img :src="htp + item.image_url" />
                <p>{{ item.title }}</p>
              </div>
            </div>
          </van-swipe-item>
        </van-swipe>
        <div class="nearbyMerchants">
          <van-icon name="shop-o" />
          <span>附近商家</span>
        </div>
        <div class="brand">
          <div class="brandList"  v-for="item in shopList" :key="item.id" @click="goBuiness(item.id)" >
            <div class="brandEvaluation">
              <img :src="htpurl + item.image_path" />
              <div class="brandEvaluationRight">
                <div class="brandEvaluationOne">
                  <span class="brandk">品牌</span>
                  <span>{{item.name}}</span>
                </div>
                <div class="brandEvaluationTwo">
                  <van-icon name="star" /><van-icon name="star" /><van-icon
                    name="star"
                  /><van-icon name="star" /><van-icon name="star" />
                  <span class="score">4.7</span>
                  <span class="scoretext">月售105单</span>
                  <p>¥{{item.float_minimum_order_amount}}起送/配送费约{{item.float_delivery_fee}}¥</p>
                </div>
              </div>
            </div>
            <div class="brandGive">
              <div class="brandGiveOne">
                <span>保</span>
                <span>准</span>
                <span>送</span>
              </div>
              <div class="brandGiveTWO">
                <span class="one">蜂鸟专送</span>
                <span class="two">准时送达</span>
                <p>10公里/40分钟</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <NavBar></NavBar>
  </div>
</template>

<script>
import { mapState } from "vuex";
import NavBar from '@/components/NavBar/index.vue'
export default {
  name: "Home",
  data() {
    return {
      htp:'https://fuss10.elemecdn.com',
      htpurl:'https://elm.cangdu.org/img/',
      navId:''
    };
  },
  components:{
    NavBar
  },
  methods: {
    getHomeList() {
      this.$store.dispatch("getHomeList");
    },
    getHomeShopList(){
      this.$store.dispatch('getHomeShopList')
    },
    //跳转到登录页面

  
    goLogin(){
      this.$router.push("/login")
    },
    // 跳转到商家详情页
    goBuiness(id){
      console.log(111);
      this.$router.push(`/shop?id=${id*1}`)
    },
    //跳转到food
    goFood(id,title){
       this.$router.push({
          path: '/food',
          query:{
            id:id,
            title:title
          }
        })

    }

  },
  mounted() {
    this.getHomeList();
    this.getHomeShopList();
  },
  computed: {
    ...mapState({
      homeList: (state) => state.home.homeList,
      shopList:(state) =>state.home.shopList,
      isLogin:()=> JSON.parse(localStorage.getItem('userInfo'))
    }),
    oneArr() {
      return this.homeList.slice(0, 4);
    },
    twoArr() {
      return this.homeList.slice(4, 8);
    },
    threeArr() {
      return this.homeList.slice(8, 12);
    },
    fourArr() {
      return this.homeList.slice(12);
    },
  },
};
</script>


