  position: fixed;
  top: 0;
  font-size: 0.65rem;
  height: 50px;
  background-color: #39a9ed;
  z-index: 999999999;
  width: 100%;
  justify-content: space-between;
  line-height: 50px;
  color: aliceblue;
}
.header .van-icon {
  font-size: 30px;
  color: aliceblue;
  margin-top: 10px;
  margin-left: 10px;
}
