.menuStyle .menuList {
  width: 25%;
  height: 100px;
  position: relative;
  padding-top: 10px;
}
.menuStyle .menuList img {
  width: 30px;
  height: 30px;
  // border: 1px solid red;
  display: block;
  // border-radius: 50%;
  position: absolute;
  left: 32%;
  top: 10%;
}
.menuStyle .menuList p {
  font-size: 16px;
  text-align: center;
  /* align-items: center; */
  margin: 55px auto;
}
.nearbyMerchants {
  width: 100%;
  height: 30px;
  margin-top: 20px;
  background-color: white;
  font-size: 0.7rem;
  line-height: 30px;
}
.nearbyMerchants .van-icon {
  margin: auto 0.35rem;
}
.brandList {
  width: 100%;
  border-bottom: black 0.05rem solid;
  background-color: white;
  display: flex;
  justify-content: space-between;
  font-size: 0.7rem;
}
.brandList .brandEvaluation {
  display: flex;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.brandList .brandEvaluation img {
  width: 3rem;
  height: 3rem;
  margin-left: 0.15rem;
}
.brandEvaluationRight {
  margin-left: 0.5rem;
}
.brandList .brandEvaluation .brandEvaluationOne {
  font-size: 0.5rem;
}
.brandList .brandEvaluation .brandk {
  margin-right: 0.5rem;
  font-size: 0.3rem;
  font-weight: 600;
  background-color: yellow;
}
.brandList .brandEvaluation .brandEvaluationTwo {
  font-size: 0.2rem;
  padding-top: 0.5rem;
  position: relative;
}
.brandList .brandEvaluation .brandEvaluationTwo .van-icon {
  color: rgb(255, 123, 0);
  font-size: 0.5rem;
}
.brandList .brandEvaluation .brandEvaluationTwo p {
  margin-top: 0.4rem;
}
// 起送距离的设置
.brandGive {
  position: relative;
  margin-right: 0.2rem;
}
.brandGive .brandGiveOne {
  position: absolute;
  right: 0.05rem;
}
// 评分的样式
.score {
  position: absolute;
  top: 0.2rem;
  left: 3rem;
  color: orangered;
}
.scoretext {
  position: absolute;
  left: 4rem;
  width: 5rem;
}
.brandGive .brandGiveOne span {
  font-size: 0.3rem;
  border: rgba(21, 32, 184, 0.884) 0.025rem solid;
  margin-right: 0.1rem;
  color: rgba(21, 32, 184, 0.884);
}
.brandGive .brandGiveTWO {
  margin-top: 1.4rem;
}
.brandGive .brandGiveTWO span {
  font-size: 0.5rem;
}
.brandGive .brandGiveTWO p {
  font-size: 0.5rem;
  margin-top: 0.4rem;
