import axios from "axios";
import nProgress from "nprogress";
import 'nprogress/nprogress.css';

const reqaxios = axios.create({
    // 用于匹配代理服务器的前缀请求路径
  baseURL: '/api', // url = base url + request url
  // baseURL: 'http://localhost:8001', // url = base url + request url
//   baseURL: 'https://elm.cangdu.org', // url = base url + request url
  // 超时时间
//   timeout: 5000 // request timeout
});
// 请求拦截器
reqaxios.interceptors.request.use((config)=>{
    nProgress.start()
    return config
});
// 响应拦截器
reqaxios.interceptors.response.use(res=>{
    nProgress.done()
    return res.data
});
export default reqaxios