import good from '@/api/index'
const {getGoodDetail} = good
const state = {
     detailList:[]
}
const mutations = {
    GETGOODDETAIL(state,list){
        state.detailList = list
    }
}
const actions = {
   async getGoodDetail({commit},id){
       const result = await getGoodDetail(id)
    //    detailList = result
    commit('GETGOODDETAIL',result)

   }
}
const getters = {
    // foodsdetail(state){  
    //     // return state.detailList[0]? state.detailList[0].foods[1] : {}
    //     // return state.detailList[0]? state.detailList[0].foods[0][0] : {}
    //     // return 111
    // }
}

export default {
    state,
    mutations,
    actions,
    getters
}