<template>
    <!-- 头部 -->
    <div class="header">
        <i class="sui-icon icon-pc-chevron-left icon" @click="goBack"></i>
        <span>{{title}}</span>
        <span @click="clickFunc" class="func" v-if="func">{{func}}</span>
    </div>
</template>

<script>
export default {
  name: 'Header',
  props:{
      title:String,
      func:String
  },
  methods:{
      goBack(){
          this.$router.back()
      },
      clickFunc(){
          if(this.func){
            this.$emit('update:func',this.func)
          }
      }
  }
}
</script>

<style lang='less' scoped>
.header{
    position: fixed;
    z-index: 100;
    top: 0;
    .icon{
        font-size: 1rem;
        position: absolute;
        left: 0.5rem;
        top: 0.5rem;
    }
    font-weight: bold;
    color: #fbfbfb;
    background-color: #3190e8;
    width: 100vw;
    height: 1.95rem;
    line-height: 1.95rem;
    text-align: center;
    .func{
        position: absolute;
        right: .5rem;
    }
}
</style>
